  line-height: 3rem;
  padding: .5rem 1rem;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}

.collapsible-title {
  font-size: 24px;
  font-weight: 300;
}

.divider {
  margin: .25em .5em;
}

.btn-sm, button.btn-flat.btn-sm, a.btn-flat.btn-sm {
  padding: 0 0.5rem;
}

.small {
  font-size: 90%;
}

.side-nav.fixed a {
  padding: 0;
  color: #039be5;
  font-size: inherit;
  height: inherit;
  line-height: inherit;
}

.side-nav.fixed a:hover {
  background-color: transparent;
}

footer a {
  color: white;
  text-decoration: underline;
}

footer a:hover {
  text-decoration: none;
}

.footer-copyright .btn, .footer-copyright .btn-large {
  margin-top: .5em;
  margin-left: 1em;
}

input.datepicker {
  height: 2.5em;
}

.user.collection.with-header .collection-item {
  padding-left: 20px;
}

.user.collection.with-header .collection-item:last-child {
  padding: 10px;
}

.user .btn-floating i {
  font-size: 1.3em;
  margin-top: -0.16em;
}

.user button.btn-floating {
  margin-right: -0.5em;
  width: 30px;
  height: 30px;
}

.user .input-field {
  margin-top: 0;
}

.user input[type="text"] {
  margin-bottom: 0;
}

.collapsible-body {
  padding: 1rem 1rem 0;
}

.collapsible-body.row {
  padding: 1rem 0 0;
}

.table-sortable .header, .table-sortable .tablesorter-header {
  background-position: center left;
  padding-left: 20px;
}

.collapsible-header i.training-user-icon {
  line-height: inherit;
  margin-right: 0;
}

.no-wrap {
  white-space: nowrap;
}

.collapsible-header .btn-sm > i {
  width: inherit;
  font-size: 1.3rem;
  line-height: inherit;
  margin-right: 0;
}

td button, td a i, .action-buttons * {
  opacity: .8;
}

td button:hover, td a i:hover, .action-buttons *:hover {
  opacity: 1;
  color: #039be5;
}

table .action-buttons, ul .action-buttons {
  visibility: hidden;
  text-align: right;
}

table tr:hover .action-buttons, ul li:hover .action-buttons {
  visibility: visible;
}

ul.browser-default, .trumbowyg-box ul, .browser-default ul, .browser-default ol {
  padding-left: 20px;
  margin-top: 10px;
  margin-bottom: 15px;
}

ul.browser-default li, .trumbowyg-box ul li, .browser-default ul li {
  list-style-type: initial;
}

.browser-default ol li {
  list-style-type: inherit;
}

.browser-default p {
  padding: initial;
}

/* Home Calendar */
.calendar.collection.with-header .collection-header {
  font-size: 24px;
  font-weight: 300;
  padding-top: 25px;
  padding-bottom: 15px;
}

.calendar .collection-item {
  display: flex;
}

.calendar .collection-item .date {
  margin: -10px 10px -10px -30px;
  padding: 7px 15px 14px;
  background: lightblue;
}

.calendar .collection-item .month {
  font-size: .8em;
}

.calendar .collection-item .day {
  font-size: 1.7em;
  min-width: 1.15em;
  text-align: center;
}

.events {
  flex-basis: 100%;
}

.events .entry {
  border-bottom: 1px solid #e0e0e0;
  padding: .8em 0;
}

.events .entry:first-child {
  padding-top: .3em;
}

.events .entry:last-child {
  padding-bottom: .3em;
  border-bottom: 0;
}

/* Home Calendar End */
nav .input-field label i {
  padding: 0.1em 0.2em;
}

/* Top Search Box */
.tt-menu {
  background: white;
  top: 81% !important;
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.tt-hint {
  display: none !important;
}

.tt-input {
  color: white !important;
}

.side-nav.fixed .tt-menu .list-group-item {
  padding: 0 2em;
  display: block;
  border-bottom: solid 1px;
  line-height: 3em;
}

.typeahead .input-field {
  height: 4.3em;
}

/* Top Search Box End */
.editor-preview-side > p, .editor-preview > p, .browser-default > p {
  margin-bottom: 10px !important;
}

/* Disable Chrome's native datepicker. */
input::-webkit-calendar-picker-indicator {
  display: none;
}

input[type="date"]::-webkit-input-placeholder {
  visibility: hidden !important;
}

.dataTables_filter input[type="search"] {
  width: auto;
  height: auto;
}
